.module {
    h2 {
        font-size: 32px;
        line-height: 38.4px;
        font-weight: 800;

        @include breakpoint(tablet) {
            font-size: 24px;
            line-height: 28.8px;
        }

        @include breakpoint(phone) {
            font-size: 18px;
            line-height: 21.6px;
        }
    }

    & + & {
        margin-top: 60px;

        @include breakpoint(tablet) {
            margin-top: 55px;
        }

        @include breakpoint(phone) {
            margin-top: 50px;
        }
    }

    &:last-child {
        margin-bottom: 60px;

        @include breakpoint(tablet) {
            margin-bottom: 55px;
        }

        @include breakpoint(phone) {
            margin-bottom: 50px;
        }
    }
}