@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700;800&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav {
    display: block;
}

ul {
    list-style: none;
    list-style-type: none;
}

a {
    color: inherit;
    text-decoration: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html,
body {
    overflow-x: hidden;
}

main {
    overflow: hidden;
}

body {
    font-family: 'Lato', sans-serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: $color-white;
    background: linear-gradient(196.2deg, #0D1E48 0%, #304571 100%);

    @include breakpoint(not-desktop) {
        font-size: 16px;
        line-height: 19.2px;
    }

    @include breakpoint(phone) {
        font-size: 14px;
        line-height: 16.8px;
        background: linear-gradient(180.96deg, #17233f -0.67%, #304571 100.39%);
    }
}

.container {
    width: 100%;
    max-width: 1380px;
    padding-inline: $offset;
    margin: 0 auto;

    @include breakpoint(not-desktop) {
        padding-inline: 30px;
    }

    @include breakpoint(phone) {
        padding-inline: 20px;
    }
}

.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

img {
    max-width: 100%;
    height: auto;
}

.button {
    display: block;
    width: fit-content;
    border-radius: 100px;
    padding: 14px 40px;
    background: $color-yellow;
    color: $color-black;
    font-size: 22px;
    line-height: 26.4px;
    transition: opacity 0.3s ease-in-out;

    @include breakpoint(phone) {
        padding: 7px 40px;
        font-size: 16px;
        line-height: 19.2px;
    }

    @include hover {
        opacity: 0.6;
    }

    &--outline {
        background: none;
        color: $color-yellow;
        border: 2px solid $color-yellow;
        padding: 12px 38px;

        @include breakpoint(not-desktop) {
            padding: 5px 38px;
            font-size: 16px;
            line-height: 19.2px;
        }
    }
}

button {
    border: none;
    background: none;
    cursor: pointer;
    font-size: 20px;
    line-height: 24px;
    font-weight: 800;
    color: $color-yellow;
    font-family: 'Lato', sans-serif;
}


@include breakpoint(desktop) {
    .desktop-hide {
        display: none;
    }
}

@include breakpoint(not-desktop) {
    .desktop-only {
        display: none;
    }
}