.tiles {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 40px;
    gap: 40px;

    @include breakpoint(not-desktop) {
        margin-top: 30px;
        gap: 30px;
        grid-auto-rows: minmax(350px, auto);
    }

    &__item {
        border-radius: 10px;
        padding: 37px 35px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        @include breakpoint(desktop) {
            &:last-of-type {
                height: 357px;
            }
        }

        @for $i from 1 through 4 {
            &:nth-of-type(#{$i}) {
                background:
                linear-gradient(-90deg, rgba(0, 0, 0, 0) -0.52%, rgba(15, 20, 32, 0.79) 99.48%),
                url('../../assets/tile-#{$i}.jpg') no-repeat center/cover;
            }
        }

        @include breakpoint(not-desktop) {
            padding: 27px 25px;

            &:last-of-type {
                background:
                linear-gradient(-90deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
                url('../../assets/tile-4-m.jpg') no-repeat center/cover;
            }
        }

        h4 {
            font-size: 74px;
            line-height: 1;
            font-weight: 800;
            letter-spacing: 0.02rem;
            margin-top: auto;

            @include breakpoint(desktop) {
                max-width: 650px;
            }

            @include breakpoint(not-desktop) {
                font-size: 40px;

                @media(max-width: 340px) {
                    font-size: 36px;
                }
            }
        }

        p {
            margin-block: 22px 20px;
            letter-spacing: 0.012rem;
            font-size: 26px;
            line-height: 31.2px;

            @include breakpoint(not-desktop) {
                font-size: 16px;
                line-height: 19.2px;
                margin-block: 20px;
            }
        }


        @include breakpoint(desktop) {
            &--full {
                grid-column: span 2;
            }

            &--half {
                grid-column: span 1;
                padding: 50px 35px 40px;

                h4 {
                    font-size: 46px;
                }

                p {
                    font-size: 24px;
                    line-height: 28.8px;
                }
            }
        }

        @include breakpoint(not-desktop) {
            grid-column: span 1;
        }

        @include breakpoint(phone) {
            grid-column: span 2;
        }
    }
}