.hero {
    position: relative;
    height: calc(100lvh - #{$header-height});
    display: flex;
    align-items: center;

    @include breakpoint(tablet) {
        height: 420px;
    }

    @include breakpoint(phone) {
        height: 451px;
        align-items: flex-end;
        padding-bottom: 44px;
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: 800;

        @include breakpoint(desktop) {
            padding-right: 60px;
        }

        @include breakpoint(not-desktop) {
            padding-top: 5px;
        }

        @include breakpoint(phone) {
            justify-content: center;
        }
    }

    &__description {
        max-width: 693px;

        @include breakpoint(desktop) {
            padding-bottom: 25px;
        }

        @include breakpoint(not-desktop) {
            max-width: 411px;
            width: 411px;
        }

        @include breakpoint(phone) {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
    }

    &__background {
        position: absolute;
        inset: 0;
        z-index: -1;
        background:
            linear-gradient(-90deg, rgba(0, 0, 0, 0) -0.52%, rgba(15, 20, 32, 0.79) 99.48%),
            url('../../assets/hero-bg.jpg') no-repeat center/cover;

        @include breakpoint(phone) {
            background:
            linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
            url('../../assets/hero-bg.jpg') no-repeat center/cover;
        }

        img {
            height: 100%;
            object-fit: cover;
        }
    }

    h1 {
        font-size: 72px;
        line-height: 1;
        letter-spacing: 0.04rem;

        @include breakpoint(not-desktop) {
            font-size: 48px;
        }

        @include breakpoint(phone) {
            font-size: 36px;

            @media(max-width: 340px) {
                font-size: 32px;
            }
        }

        b {
            color: $color-pink;
        }
    }

    p {
        font-size: 32px;
        line-height: 38.4px;
        margin-block: 22px;
        letter-spacing: 0.013rem;

        @include breakpoint(not-desktop) {
            font-size: 22px;
            line-height: 26.4px;
        }

        @include breakpoint(phone) {
            font-size: 16px;
            line-height: 19.2px;
            margin-block: 17px;
        }

        b {
            color: $color-yellow;
        }
    }

    .button {
        display: flex;
        width: fit-content;
    }

    &__illustration {
        position: absolute;
        top: 12%;
        right: 4.6%;

        @include breakpoint(desktop) {
            width: 29%;
        }

        svg {
            width: 100%;
            height: auto;

            @include breakpoint(desktop) {
                min-width: 361px;
            }

            @include breakpoint(not-desktop) {
                width: 330px;
            }
        }

        @include breakpoint(not-desktop) {
            right: -14%;
            top: 4%;
            margin-top: 10px;
        }

        @include breakpoint(phone) {
            right: auto;
            left: 4%;
            top: -28%;
        }

        .rocket {
            position: absolute;
            top: 64%;
            left: 27%;
            width: 33%;
        }
    }
}