.text {
    margin-top: 17px;

    p {
        opacity: 0.6;

        &:nth-child(1n + 2) {
            max-height: 0;
            opacity: 0;
            pointer-events: none;
            transform: translateY(15px);
            transition-delay: 0s !important;
            transition: opacity 0s ease-in-out, transform 0.3s ease-in-out;
        }
    }

    button {
        margin-top: 17px;
    }

    .loaded { display: none; }

    #load-more {
        display: none;

        &:checked ~ ul {
            p:nth-child(1n + 2) {
                max-height: 999px;
                opacity: 0.6;
                pointer-events: all;
                padding-top: 17px;
                transform: translateY(0);
                transition-delay: 0;
                transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
            }
        }

        &:checked ~ label[data-more] {
          .loaded { display: block; }
          .unloaded { display: none; }
        }
    }

    label {
        display: block;
        margin-top: 17px;
        color: $color-yellow;
        width: fit-content;
        cursor: pointer;
        transition: opacity 0.3s ease-in-out;

        @include hover {
            opacity: 0.6;
        }
    }
}
