$itemMobileHeight: 48px;
$headerItems: 3;

.header {
    position: relative;
    background: $color-black;
    height: $header-height;
    z-index: 2;

    @include breakpoint(phone) {
        height: $itemMobileHeight;
    }

    &__button {
        display: none;
        padding: 10px 4px;
        width: 30px;
        flex-direction: column;
        gap: 7px;
        overflow: hidden;

        @include breakpoint(phone) {
            display: flex;
        }

        span {
            display: block;
            background: $color-white;
            border-bottom: 1.5px solid $color-white;
            border-radius: 1.5px;
            width: 100%;
            transition: transform 0.2s ease-in-out;

            &:nth-child(1) {
                transform-origin: left;
            }

            &:nth-child(3) {
                transform-origin: left;
            }
        }
    }

    input {
        display: none;

        &:checked + label {
            span {
                border-radius: 0;
            }

            span:nth-child(1) {
                transform: translate(2px, 1px) rotate(36deg);
                margin-top: 0;
            }

            span:nth-child(2) {
                transform: translateX(-120%);
            }

            span:nth-child(3) {
                transform: translate(2px, -2px) rotate(-36deg);
                margin-top: 0;
            }
        }

        &:checked ~ .header__nav ul {
            height: calc(#{$itemMobileHeight} * #{$headerItems});
            transition: height 0.3s ease-in-out;

            a {
                opacity: 1;
                transform: translateX(0);
                transition-delay: 0.2s;
            }
        }
    }

    &__nav {
        @include breakpoint(phone) {
            position: absolute;
            inset: 100% 0 0;
        }
    }

    &__logo {
        width: 106px;

        a {

            svg {
                overflow: visible;
            }
            .globe {
                transition: transform 0.3s ease-in-out;
                transform-origin: 18% 49%;
            }

            @include hover {
                .globe {
                    transform: rotate(360deg);
                }
            }
        }

        @include breakpoint(not-desktop) {
            width: 71px;
        }
    }

    li a {
        color: $color-white;

        @include breakpoint(phone) {
            opacity: 0;
            transform: translateX(10px);
            transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
        }

        @include hover {
            background: linear-gradient(98.25deg, #EBFF00 18.56%, #FF70D9 76.02%);
            background-clip: text;
            -webkit-background-clip: text;
            color: transparent;

            svg path {
                fill: url(#gradient);
            }
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include breakpoint(phone) {
            height: 100%;
        }
    }

    ul {
        display: flex;
        column-gap: 57px;
        list-style: none;
        font-size: 18px;
        line-height: 21.6;

        a,
        span {
            display: flex;
            align-items: center;
            height: 100%;
        }

        @media (min-width: 480px) {
            margin-right: 10px;
        }

        @include breakpoint(phone) {
            display: flex;
            height: fit-content;
            flex-direction: column;
            padding: 0 20px;
            position: absolute;
            inset: 100% 0 0;
            background: $color-black;
            height: 0;
            transition: height 0.3s ease-in-out 0.2s;
        }
    }

    &__nav-item {
        display: flex;
        align-items: center;
        height: 80px;
        overflow: hidden;

        svg {
            display: block;
            margin-top: 5px;
        }

        @include breakpoint(tablet) {
            &:last-of-type {
                position: relative;
                left: -10px;
            }
        }

        @include breakpoint(phone) {
            height: $itemMobileHeight;
        }
    }
}