@mixin hover {
    @include breakpoint(desktop) {
        &:hover {
            @content;
        }
    }
}

@mixin breakpoint($point) {

    @if $point == phone {
        @media (max-width: $size-tablet - 1px) { @content; }
    } @else if $point == not-phone {
        @media (min-width: $size-tablet) { @content; }
    } @else if $point == tablet {
        @media (min-width: $size-tablet) and (max-width: $size-desktop - 1px) { @content; }
    } @else if $point == desktop {
        @media (min-width: $size-desktop) { @content; }
    } @else if $point == not-desktop {
        @media (max-width: $size-desktop - 1px) { @content; }
    } @else if $point == tablet-landscape {
        @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) and (pointer: coarse) { @content; }
    }
}